@import url("https://fonts.googleapis.com/css?family=Acme");

@font-face {
  font-family: 'norwesterregular';
  src: url('./fonts/norwester-webfont.eot');
  src: url('./fonts/norwester-webfont.eot?#iefix') format('embedded-opentype'),
       url('./fonts/norwester-webfont.woff2') format('woff2'),
       url('./fonts/norwester-webfont.woff') format('woff'),
       url('./fonts/norwester-webfont.ttf') format('truetype'),
       url('./fonts/norwester-webfont.svg#norwesterregular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

/* BASIC STYLES */

html {
  max-height: 100%;
  max-width: 100%;
  height: 100%;
}

body {
  height: 100%;
  position: relative;
}

/* WRAPPER */

.wrapper {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 100%;
  padding: 0 0 100px;
  position: relative;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



